<template>
  <v-app-bar app color="primary" dark>
    <!-- Toolbar Title -->
    <v-toolbar-title>{{ $t("header.title") }}</v-toolbar-title>

    <!-- Spacer -->
    <v-spacer></v-spacer>

    <!-- Desktop Navigation -->
    <template v-if="$vuetify.breakpoint.mdAndUp">
      <v-btn text to="/about">{{ $t("header.about") }}</v-btn>
      <v-btn text to="/map">{{ $t("header.map") }}</v-btn>
    </template>

    <!-- Mobile Navigation -->
    <template v-else>
      <v-btn icon @click.stop="drawer = !drawer">
        <v-icon>mdi-menu</v-icon>
      </v-btn>
    </template>

    <!-- Language Selector -->
    <v-menu offset-y class="header-languages">
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on">
          <v-icon>mdi-earth</v-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item @click="setLanguage('en')">
          <v-list-item-title>English</v-list-item-title>
        </v-list-item>
        <v-list-item @click="setLanguage('ru')">
          <v-list-item-title>Русский</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <!-- Navigation Drawer for Mobile -->
    <v-navigation-drawer
        v-model="drawer"
        app
    >
      <v-list>
        <v-list-item-group v-model="group">
          <v-list-item to="/about">
            <v-list-item-title>{{ $t("header.about") }}</v-list-item-title>
          </v-list-item>
          <v-list-item to="/map">
            <v-list-item-title>{{ $t("header.map") }}</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </v-app-bar>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      drawer: false,
      group: null,
    };
  },
  watch: {
    group () {
      this.drawer = false
    },
  },
  methods: {
    setLanguage(lang) {
      this.$i18n.locale = lang;
    },
    navigate(route) {
      // Close the drawer after navigation
      console.log('Navigate to', route);
      this.$router.push(route);
      this.drawer = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.header {
  &-languages {
    z-index: 1000 !important;
  }
}

.v-navigation-drawer {
  z-index: 2000 !important; /* Ensure it appears above other elements */
}
</style>
